.hands-container {
  @keyframes spin {
    0% {
      transform: rotate(0deg);
    }
    100% {
      transform: rotate(360deg);
    }
  }

  font-family: "Titillium Web", sans-serif;
  color: white;
  margin: 0;
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
  top: 0;
  transform-origin: 0px 0px;
  overflow: hidden;
  background-color: #282c34;
  width: 100%;
  max-height: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  video {
    display: none;
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    &.selfie {
      transform: scale(-1, 1);
    }
  }

  canvas {
    width: 80%;
    display: block;
    position: relative;
    left: 0;
    top: 0;
  }

  .control-panel {
    position: absolute;
    left: 10px;
    top: 10px;
  }

  .loading {
    display: flex;
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    align-items: center;
    backface-visibility: hidden;
    justify-content: center;
    opacity: 1;
    transition: opacity 1s;

    .message {
      font-size: x-large;
    }

    .spinner {
      position: absolute;
      width: 120px;
      height: 120px;
      animation: spin 1s linear infinite;
      border: 32px solid #bebebe;
      border-top: 32px solid #214054;
      border-radius: 50%;
    }
  }

  .square-box {
    width: 33%;
    height: 0;
    padding-top: 33%;
    position: absolute;
    right: 20px;
    top: 20px;
  }

  .landmark-grid-container {
    height: 100%;
    width: 100%;
    position: absolute;
    top: 0;
    left: 0;
    background-color: #99999999;
  }

  .left-elbow-angle {
    position: absolute;
    top: 0;
    left: 0;
    color: #fff;
    font-size: 20px;
    font-weight: bold;
    background-color: #000;
    padding: 5px;
  }
}
